<template>
  <v-container fluid class="py-5">
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-toolbar
      color="deep-purple accent-4"
      dark
      flat
    >

      <v-toolbar-title class="text-h6 font-weight-bold">
        <v-icon left class="mr-2">mdi-cash-multiple</v-icon>
        Fees Payment Summary For Year {{ academicYear }}
      </v-toolbar-title>
      <template v-slot:extension>
        <v-tabs
          background-color="deep-purple accent-4"
          centered
          dark
          icons-and-text
          v-model="tab"
        >
          <v-tabs-slider></v-tabs-slider>

          <v-tab key="approved">
            Approved Payments
            <v-icon>mdi-web-check</v-icon>
          </v-tab>

          <v-tab key="others">
            Pending Approval Payments
            <v-icon>mdi-web-cancel</v-icon>
          </v-tab>

          <v-tab key="all">
            Approved And Pending Payments
            <v-icon>mdi-select-all</v-icon>
          </v-tab>
        </v-tabs>
      </template>
    </v-toolbar>
    <v-tabs-items v-model="tab">
      <v-tab-item key="approved">
        <FeesSummary :summary="summary.approved" :overlay="overlay" title="Approved Payments Status"></FeesSummary>
      </v-tab-item>
      <v-tab-item key="others">
        <FeesSummary :summary="summary.others" title="Pending Approval Payments Status"></FeesSummary>
      </v-tab-item>
      <v-tab-item key="all">
        <FeesSummary :summary="summary.all" title="Pending And Approved Payments Status"></FeesSummary>
      </v-tab-item>
    </v-tabs-items>
  </v-container>
</template>

<script>
export default {
  data: function() {
    return {
      tab: null,
      loading: false,
      academicYear: "",
      overlay: false,
      summary: {
        approved: {},
        all: {}
      }
    };
  },
  mounted: function() {
    this.getData(true);
  },
  computed: {
    role() {
      let role = this.$store.state.user.obj.resource.extension.find((ext) => {
        return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-assign-role'
      }).valueReference.reference
      return role.split("/")[1]
    },
    approvedPaymentPercentage() {
      return (this.summary.approved.paid / this.summary.approved.required) * 100;
    }
  },
  components: {
    "FeesSummary": () => import("./FeesSummary.vue")
  },
  methods: {
    getData() {
      this.overlay = true
      let url = "/site/student/getFeesPaymentsSummary"
      fetch(url).then((response) => {
        if (response.status === 200) {
          response.json().then((response) => {
            this.overlay = false
            this.summary = response
          })
        } else {
          this.overlay = false
          this.$store.commit("setError", "Unable to load data")
        }
      })
      .catch(() => {
        this.overlay = false
        this.$store.commit("setError", "Unable to load data")
      });
    }
  },
  created() {
    fetch("/site/student/getAcademicYear").then((response) => {
      response.json().then((response) => {
        this.academicYear = response.academicYear
        let end = response.end.split("-")
        let year = parseInt(end[0]) - 1
        this.startDate = year + "-" + end[1] + "-" + end[2]
        this.endDate = response.end
      })
    })
  }
};
</script>
